










/* eslint-disable */
import {  Component,  Vue, Prop} from 'vue-property-decorator';
import { RoomDto, RoomPlayDto } from '@/models'
import {  charactersService } from '@/services/characters-service.ts';

@Component({})
export default class EditRoomDialog extends Vue {
  @Prop() textConfirm?: string;

  private isLoading: boolean = false;

  private RoomPlayDto: RoomPlayDto = new RoomPlayDto();

  private listCharacter: any[] = [];
  private chooseCharaters: any = '';

  private mounted() {
    charactersService.getAllCharacters()
    .then((res: any) => {
      this.listCharacter = res.data.chaName;
    })
    .catch((err: any) => {
      console.log(err);
    })
  }

  private cancel() {
    this.$emit('cancel');
  }

  private confirm() {
    this.$emit('confirm');
  }

}
