import Vue from 'vue';
import { AxiosPromise } from 'axios';
import authHeader from './auth-header';
import { CharactersDto } from '@/models';

class CharactersService extends Vue {
  private ROOT_API = process.env.VUE_APP_API_URL + '/characters';  // Root api 

  public getAllCharacters(): AxiosPromise<any> {
    return Vue.axios.get<any>(this.ROOT_API + '/get/all', { headers: authHeader()});
  }

  public getCharactersById(id: number): AxiosPromise<CharactersDto> {
    return Vue.axios.get<CharactersDto>(this.ROOT_API + '/get/' + id, { headers: authHeader()});
  }

  // public insertBookingTime(roomBooking: RoomBookingDto): AxiosPromise<RoomBookingDto> {
  //   return Vue.axios.post<RoomBookingDto>(this.ROOT_API + '/insert', roomBooking, { headers: authHeader()});
  // }



}
export const charactersService = new CharactersService();