/* eslint-disable */
import Vue from 'vue';
import { AxiosPromise } from 'axios';
import authHeader from './auth-header';
import { RoomPlayDto } from '@/models'

class RoomPlayService extends Vue {
  private ROOT_API = process.env.VUE_APP_API_URL +'/room-play';  // Root api 

  public getAllRoom(): AxiosPromise<any> {
    return Vue.axios.get<any>(this.ROOT_API + '/get/all', { headers: authHeader()});
  }

  public getListByRoomId(roomId: number): AxiosPromise<any> {
    return Vue.axios.get<any>(this.ROOT_API + '/get/' + roomId, { headers: authHeader()});
  }

  public getListByRoomIdAndStatus(roomId: number, status: number): AxiosPromise<any> {
    return Vue.axios.get<any>(this.ROOT_API + '/get/' + roomId + '/' + status, { headers: authHeader()});
  }

  public getRPByRoomIdAndStatus(roomId: number, status: number, userId: number): AxiosPromise<any> {
    return Vue.axios.get<any>(this.ROOT_API + '/get/' + roomId + '/' + status + '/' + userId, { headers: authHeader()});
  }

  public updateRoomplay(roomPlayDto: RoomPlayDto): AxiosPromise<any> {
    return Vue.axios.put<RoomPlayDto>(this.ROOT_API + '/update/', roomPlayDto, { headers: authHeader()});
  }

}
export const roomPlayService = new RoomPlayService();