








































/* eslint-disable */
import {  Component,  Vue} from 'vue-property-decorator';

import {  UserDto } from '@/models/UserDto.ts';

import {  userService } from '@/services/user-service.ts';
import {  authService } from '@/services/auth-service.ts';
import authRole from '@/services/auth-role.ts';
import { roomService } from '@/services/room-service'

import {  AxiosResponse } from 'axios';
import EventBus from '@/EventBus';

import CreateRoomDialog from '@/views/dialog/CreateRoomDialog.vue'
import EditRoomDialog from '@/views/dialog/EditRoomDialog.vue'
import { roomPlayService } from '../services/roomplay-service';

@Component({
  components: {
    CreateRoomDialog,
    EditRoomDialog
  }
})
export default class AdminHome extends Vue {
  private user: UserDto = new UserDto();
  private token: any = '';
  private isLoading: boolean = false;

  private fullName: string = '';
  private isLogged: boolean = false;

  private dialogCreateRoomVisible: boolean = false;
  private dialogEditRoomVisible: boolean = false;
  private textConfirm: string = '';

  private listRoom: any = '';

  private mounted() {
    roomService.getAllRoom()
    .then((res: any) => {
      if (res.status === 200) {
        this.listRoom = res.data;
      }
    })
    this.checkLogged();

    EventBus.$on('logged', () => {
      this.isLogged = true;
      this.getCurrentUser();
    })

    EventBus.$on('logout', () => {
      this.isLogged = false;
    })
  }

  private checkLogged() {
    if (!localStorage.getItem('token') && !localStorage.getItem('userId')) {
      this.$router.push( {name: 'login'});
    }
    else {
      this.isLogged = true;
      this.getCurrentUser();
    }
  }

  private getCurrentUser() {
      let temp: any = localStorage.getItem('userId') ? localStorage.getItem('userId') : '';
      userService.getUserById(temp)
      .then((res: any) => {
        this.fullName = res.data.fullName;
      })
  }

  private onClickCreateRoom() {
    this.dialogCreateRoomVisible = true;
  }

  private onClickEditRoom(roomId: number) {
    this.dialogEditRoomVisible = true;
  }

  private onClickResetRoom(roomId: number) {
    this.isLoading = true;
    roomPlayService.getListByRoomId(roomId)
    .then((res: any) => {
      let listRoomById: any = res.data;
      listRoomById.forEach((element: any) => {
        this.$set(element, 'status', 0);
        this.$set(element, 'userId', 0);
        roomPlayService.updateRoomplay(element);
      });
    })
    .finally(() => {
      this.isLoading = false;
    })

  }

  private onClickDeleteRoom(roomId: number) {
    alert('Mệt vl chưa làm được');
  }

  private backWindow() {
    window.history.back();
  }

  private cancel() {
    this.dialogCreateRoomVisible = false;
  }

  private confirm() {
    this.dialogCreateRoomVisible = false;
      this.showSuccessMsg();
  }

   private showSuccessMsg() {
    this.$notify({
      title: 'Success',
      message: 'Create success',
      type: 'success'
    });
  }

}
